@font-face {
  font-family: "Helvetica";
  src: url("../fonts/Helvetica-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Oswald";
  src: url("../fonts/Oswald-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Oswald";
  src: url("../fonts/Oswald-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Oswald";
  src: url("../fonts/Oswald-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

* {
  margin: 0;
  border: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  width: 100%;
  height: 100%;
  font-size: 100%;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea,
input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
  max-width: 100%;
}

@media (max-width: 992px) {
  html {
    font-size: 12px;
  }
}

@media (max-width: 576px) {
  html {
    font-size: 9px;
  }
}

.banner .banner__grid {
  display: grid;
  grid-gap: 16px;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(3, 1fr);
  max-height: 445px;
}

@media (max-width: 992px) {
  .banner .banner__grid {
    grid-template-rows: repeat(4, 1fr);
    max-height: 630px;
  }
}

@media (max-width: 576px) {
  .banner .banner__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

.banner .banner__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: 576px) {
  .banner .banner__item-row {
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
}

.banner .banner__price {
  font-weight: 500;
  font-size: 6.4rem;
  line-height: 105.7%;
}

.banner .banner__title {
  font-weight: 500;
  font-size: 2.4rem;
  line-height: 111.5%;
  text-transform: uppercase;
  color: #91f8c8;
}

.banner .banner__description {
  margin-bottom: 1.46667rem;
  font-family: "Helvetica";
  font-weight: 400;
  font-size: 1.33333rem;
  line-height: 126%;
}

.banner .banner__description span {
  display: block;
  font-weight: 600;
  color: #c82222;
}

.banner .banner__discount {
  font-size: 2.4rem;
  line-height: 111.5%;
}

.banner .banner__item_one {
  grid-column: 1 / 1;
  grid-row: 1 / 4;
  padding: 0.8rem 1.33333rem 0.8rem 3.2rem;
  font-family: "Oswald";
  text-align: right;
  color: #fff;
  background: #000;
}

@media (max-width: 992px) {
  .banner .banner__item_one {
    grid-column: 1 / 5;
    grid-row: 4 / 4;
    text-align: center;
  }
}

@media (max-width: 576px) {
  .banner .banner__item_one {
    grid-column: 1 / 3;
    grid-row: 1 / 2;
  }
}

.banner .banner__item_two {
  grid-column: 2 / 2;
  grid-row: 1 / 3;
}

@media (max-width: 992px) {
  .banner .banner__item_two {
    grid-column: 1 / 1;
    grid-row: 1 / 4;
  }
}

@media (max-width: 576px) {
  .banner .banner__item_two {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
  }
}

.banner .banner__item_tree {
  grid-column: 3 / 3;
  grid-row: 1 / 3;
}

@media (max-width: 992px) {
  .banner .banner__item_tree {
    grid-column: 2 / 4;
  }
}

@media (max-width: 576px) {
  .banner .banner__item_tree {
    grid-column: 1 / 3;
    grid-row: 3 / 3;
  }
}

.banner .banner__item_four {
  grid-column: 4 / 4;
  grid-row: 1 / 4;
}

@media (max-width: 576px) {
  .banner .banner__item_four {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
  }
}

.banner .banner__item_five {
  grid-column: 2 / 4;
  grid-row: 3 / 3;
  padding-top: 1.06667rem;
  text-align: center;
  color: #000;
}

@media (max-width: 576px) {
  .banner .banner__item_five {
    grid-column: 1 / 3;
    grid-row: 4 / 5;
  }
}

.banner .banner__subtitle {
  margin-bottom: 1.06667rem;
  font-family: "Oswald";
  font-weight: 600;
  font-size: 3.46667rem;
  letter-spacing: -3px;
  text-transform: uppercase;
}

.banner .banner__text {
  font-family: "Helvetica";
  font-weight: 400;
  font-size: 1.46667rem;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1336px;
}
